@import "../../../assets/sass/utils/variables";
@import "../../../assets/sass/utils/mixin";

.networkLinks {
  display: flex;
  gap: 16px;
}

.networkLink {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 1px solid $light-dark-semistroke;
}

@media (min-width: 768px) {
  .networkLink {
    width: 40px;
    height: 40px;
  }
}
